import { asideMenuConfig } from "../config/menu";
export default {
  name: "playground",
  props: {
    loading: Boolean,
    content: String
  },
  data() {
    return {
      asideMenu: {},
      group: []
    };
  },
  async created() {
    const menuConfig = await asideMenuConfig();
    this.asideMenu = menuConfig.menu;
    this.group = menuConfig.group;
  }
};
